.container_spons{
    height: 200vh;
    width: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: black;
    padding-top: 100px;
    padding-bottom: 100px;
}
.container{
    margin-top: 20px;
    margin: 40px;
    
}


.spons_wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    width: 90%;
}

.spons_col{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 250px;
    padding: 10px;
    width: 260px;
    margin-left: 50px;
    margin-top: 30px;
}
.spons_title{
  color: aliceblue;
  font-size: 50px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.sponsimg{
    height: 250px;
    width: 260px;
    object-fit: cover;
    border-radius: 5px;
}

@media (max-width:900px) {
    .container_spons{
        height: 300vh;
        width: 100%;
        margin: 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        background-color: black;
    }
    
    .spons_wrapper{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
        width: 90%;
        flex-direction: column;
    }
    
    .spons_col{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 220px;
        width: 220px;
        margin-left: 0px;
        margin-top: 20px;
    }
    .sponsimg{
        height: 220px;
        width: 220px;
        object-fit: cover;
        border-radius: 5px;
    }
    .spons_title{
      color: aliceblue;
      font-size: 50px;
      margin-top: 30px;
      margin-bottom: 30px;
    }
    
   
}