*{
    padding: 0;
    margin: 0;
    
}

.carousel{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 130vh;
   flex-direction: column;
   padding-top: 100px;
}
.Main_box{
    /* background-color: blue; */
    padding: 10px;
    width: 60%;
    height:105vh;
    position: relative;
    display: flex;
    flex-direction: column;
}

.mobilestepper{
    background: #000;
}
.labelcompo{
   position: relative;
top: 65%;
   z-index: 99;
   display: flex;
   flex-direction: column;
   padding:0px 20px;
}
.gallery_images_label{
   color: aliceblue;
   font-size: 22px;
}

p{
    color: aliceblue;
    width: 300px;
    height: 30px;
}
.box_images{
    height: 255;
    display: 'block';
    width: 100%;
    overflow: 'hidden';
    width: '100%';
    height:75vh;
    object-fit: cover;
    /* position: absolute; */
}

.eventstitle{
    text-align: center;
    color: aliceblue;
}


@media (max-width:900px) {
    
    .carousel{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100vh;
       flex-direction: column;
       padding: 30px;
    }
    .Main_box{
        /* background-color: blue; */
        padding: 10px;
        width: 95%;
        height:80vh;
        position: relative;
        display: flex;
        flex-direction: column;
    }
    
    .mobilestepper{
        background: #000;
    }
    .labelcompo{
       position: relative;
       top: 70%;
       z-index: 99;
       /* background-color: blue; */
       padding:0px 20px 20px;
    }
    .gallery_images_label{
       color: aliceblue;
       font-size: 22px;
    }
    .box_images{
        height: 255;
        display: 'block';
        width: 100%;
        overflow: 'hidden';
        width: '100%';
        height:55vh;
        object-fit: cover;
        /* position: absolute; */
    }
    
    .eventstitle{
        text-align: center;
    }
    p{
        color: aliceblue;
        width: 220px;
        height: 30px;
    }
}